import { Typography } from "@material-ui/core";
import { Alert } from "@material-ui/core";
import { Box, Card, CardHeader, Grid, IconButton } from "@material-ui/core";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import day1 from "../images/day1.svg";


const data = [1, 2, 3, 4, 5, 6];

const Item = () => (
    <Grid item color='white' backgroundColor='black' className='rounded-lg border-white border-2 p-3' style={{ minWidth: '21rem' }}>
        <div className='flex justify-between w-100 space-x-2'>
            <div className='w-6/12'>
                <Typography variant='p' className='font-medium' sx={{ color: 'error.main' }}>July 6th, 08:37am</Typography>
                <Typography variant='p' className='block text-3xl font-medium' sx={{ color: 'error.contrastText' }}>Laurel, US</Typography>
            </div>
            <div className='w-5/12 text-center self-center'>
                <Typography variant='p' className='block text-4xl font-medium' sx={{ color: 'warning.light' }}>40&deg;C</Typography>
            </div>
            <div className='w-1/12'>
                <IconButton id="long-button" onClick={""}>
                    <MoreVertIcon className='text-white' />
                </IconButton>
            </div>
        </div>

        <div className='flex'>
            <div className='w-5/12 bg-red-90 flex flex-col items-center mt-3'>
                <img width='80' src={day1} alt='weather' />
                <Typography variant='p' className='text-2xl' sx={{ marginTop: '-15px' }}>Sunny</Typography>
            </div>
            <div className='w-7/12 flex flex-col space-y-2'>
                <Typography variant='p' className=' mb-5 text-center '>Feels like 14Â°C. Light rain, gentle breeze.</Typography>
                <Typography>Humidity:</Typography>
                <Typography>Windspeed:</Typography>
                <Typography>Visibility:</Typography>
            </div>
        </div>
    </Grid>
)
function Dashboard() {
    return (
        <div>
            <div className='flex flex-col items-center space-y-2'>
                <Typography variant='p' className='self-center text-3xl border-4 border-blue-70
                font-medium text-white bg-black p-2 rounded-lg'>DashBoard</Typography>
                <Alert severity='info' className='' >Click on A city to View its Weekly Forecast</Alert>
            </div>
            <Grid className='flex justify-start p-3 overflow-auto  space-x-3'>
                {data.map(item => <Item />)}
            </Grid>
        </div>
    )
};


export default Dashboard;